import React, { useState } from 'react';
import { Container, Row, Col, Card, Form, Alert, Table } from 'react-bootstrap';
import { parseCSV } from '../utils/csvParser';
import * as DataPrep from '../utils/dataPreparation';
import PieChart from './charts/PieChart';
import LineChart from './charts/LineChart';
import BarChart from './charts/BarChart';
import WorldMap from './WorldMap';

const Dashboard = () => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = (e) => {
      const text = e.target.result;
      try {
        const parsedData = parseCSV(text);
        setData(parsedData);
        setError(null);
      } catch (err) {
        setError("Error parsing CSV: " + err.message);
      }
    };
    reader.readAsText(file);
  };

  return (
    <Container fluid>
      <Row className="mb-3">
        <Col>
          <h1 className="mt-3">Email Analytics Dashboard</h1>
          <Form.Group controlId="formFile" className="mb-3">
            <Form.Label>Upload CSV file</Form.Label>
            <Form.Control type="file" onChange={handleFileUpload} accept=".csv" />
          </Form.Group>
          {error && <Alert variant="danger">{error}</Alert>}
        </Col>
      </Row>

      {data.length > 0 && (
        <>
          <Row>
            <Col md={6}>
              <Card className="mb-3">
                <Card.Header>Activity Summary</Card.Header>
                <Card.Body>
                  <Table striped bordered hover>
                    <tbody>
                      {DataPrep.prepareActivitySummary(data).map((item, index) => (
                        <tr key={index}>
                          <td>{item.name}</td>
                          <td>{item.value.toLocaleString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Card.Body>
              </Card>
            </Col>
            <Col md={6}>
              <Card className="mb-3">
                <Card.Header>Engagement</Card.Header>
                <Card.Body>
                  <Row>
                    <Col md={6}>
                      <h6>Read Time Falloff</h6>
                      <LineChart data={DataPrep.prepareReadTimeFalloff(data)} xKey="time" yKey="percentage" />
                    </Col>
                    <Col md={6}>
                      <h6>Engagement Time Distribution</h6>
                      <PieChart data={DataPrep.prepareEngagementDistribution(data)} />
                    </Col>
                  </Row>
                </Card.Body>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col md={6}>
              <Card className="mb-3">
                <Card.Header>Daily Activity</Card.Header>
                <Card.Body>
                  <LineChart data={DataPrep.prepareDailyActivity(data)} xKey="date" yKey="opens" />
                </Card.Body>
              </Card>
            </Col>
            <Col md={6}>
              <Card className="mb-3">
                <Card.Header>Top Email Clients</Card.Header>
                <Card.Body>
                  <BarChart data={DataPrep.prepareEmailClientData(data)} />
                </Card.Body>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col md={4}>
              <PieChart data={DataPrep.prepareReadingEnvironmentData(data)} title="Reading Environment" />
            </Col>
            <Col md={4}>
              <PieChart data={DataPrep.prepareRenderingEnginesData(data)} title="Rendering Engines" />
            </Col>
            <Col md={4}>
              <PieChart data={DataPrep.prepareBrowserUsageData(data)} title="Browser Usage" />
            </Col>
          </Row>

          <Row>
            <Col md={12}>
              <Card className="mb-3">
                <Card.Header>Top Countries</Card.Header>
                <Card.Body>
                  <div style={{ height: '400px' }}>
                    <WorldMap data={DataPrep.prepareCountryData(data)} />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </>
      )}
    </Container>
  );
};

export default Dashboard;