import React from 'react';
import { PieChart as RechartsChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { Card } from 'react-bootstrap';

const COLORS = ['#A0A0A0', '#9FA8DA', '#4FC3F7', '#81C784', '#FFB74D', '#FF8A65'];

const PieChart = ({ data, title }) => {
  const total = data.reduce((sum, entry) => sum + entry.value, 0);

  return (
    <Card className="mb-3">
      <Card.Header>{title}</Card.Header>
      <Card.Body>
        <ResponsiveContainer width="100%" height={300}>
          <RechartsChart>
            <Pie
              data={data}
              cx="50%"
              cy="50%"
              outerRadius={80}
              fill="#8884d8"
              dataKey="value"
            >
              {data.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
              ))}
            </Pie>
            <Tooltip 
              formatter={(value, name) => {
                const percent = ((value / total) * 100).toFixed(1);
                return [`${value} (${percent}%)`, name];
              }}
            />
            <Legend
              layout="vertical"
              align="right"
              verticalAlign="middle"
              formatter={(value, entry, index) => {
                const percent = ((entry.payload.value / total) * 100).toFixed(1);
                return (
                  <span style={{ color: COLORS[index % COLORS.length], fontSize: '12px' }}>
                    {value} ({percent}%)
                  </span>
                );
              }}
            />
          </RechartsChart>
        </ResponsiveContainer>
      </Card.Body>
    </Card>
  );
};

export default PieChart;