import React, { useEffect, useState } from 'react';
import { ComposableMap, Geographies, Geography } from "react-simple-maps";
import { scaleLinear } from "d3-scale";
import { Tooltip } from "react-tooltip";

const geoUrl = "https://raw.githubusercontent.com/BolajiBI/topojson-maps/master/world-countries-sans-antarctica.json";

const WorldMap = ({ data }) => {
  const [geographies, setGeographies] = useState([]);

  useEffect(() => {
    fetch(geoUrl)
      .then(response => response.json())
      .then(geoData => {
        setGeographies(geoData);
      })
      .catch(error => console.error("Error loading map data:", error));
  }, []);

  useEffect(() => {
    console.log('WorldMap component mounted or data changed');
    console.log('Received data in WorldMap:', data);
  }, [data]);

  if (!data || data.length === 0) {
    console.log('No data received in WorldMap');
    return <div>No data available for the map</div>;
  }

  const maxValue = Math.max(...data.map(d => d.value));
  console.log('Max value for color scale:', maxValue);

  const colorScale = scaleLinear()
    .domain([0, maxValue])
    .range(["#ffedea", "#ff5233"]);

  return (
    <>
      <ComposableMap data-tooltip-id="my-tooltip" projectionConfig={{ scale: 147 }}>
        {geographies.length > 0 && (
          <Geographies geography={geographies}>
            {({ geographies }) => {
              console.log('Number of geographies:', geographies.length);
              return geographies.map((geo) => {
                const d = data.find(s => s.id === geo.id);
                console.log(`Checking geography: ${geo.properties.name} (${geo.id}), match: ${d ? 'Yes' : 'No'}`);
                return (
                  <Geography
                    key={geo.rsmKey}
                    geography={geo}
                    fill={d ? colorScale(d.value) : "#F5F4F6"}
                    data-tooltip-content={d ? `${d.name}: ${d.value}` : geo.properties.name}
                  />
                );
              });
            }}
          </Geographies>
        )}
      </ComposableMap>
      <Tooltip id="my-tooltip" />
    </>
  );
};

export default WorldMap;